.wishlist > .item_container {
  width: 95%;
  margin: 0 auto;
}

.wishlist > .item_container .item {
  display: flex;
  /* justify-content: space-around; */
  justify-content: space-between;
  margin-bottom: 20px;
}

.wishlist > .item_container .price_wrapper > span {
  color:dimgray; 
}

.wishlist > .item_container .price_wrapper span + span {
  color:red; 
}

.wishlist > .item_container .item_info {
  width: 260px;
  margin-top: 20px;
  margin-right: auto;
}

.wishlist > .item_container .item_info a {
  font-weight: bold;
}

.wishlist > .item_container button {
  align-self: center;
  width:50px;
  height: 50px;
  background-color: transparent;
  border: none;
}

@media (min-width:600px) {
  .wishlist > .item_container {
    width: 80%;
    margin: 0 auto;
  }
}

@media (min-width:1200px) {
  .wishlist > .item_container {
    width: 1000px;
    margin: 0 auto;
  }
}